export default {
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: "消息列表"
		});
		
		var userInfo = this.getGlobalUserInfo()
		this.userInfo = userInfo;
		
		var currentTab = data.currentTab;
		
		// 获取userId
		var userId = thisUserInfo.id;
		this.queryFansFollow(userId);
		
		// 设置列表 index
		this.currentTab = currentTab;
		
		var screenWidth = uni.getSystemInfoSync().screenWidth;
		this.screenWidth = screenWidth;
		
		// 获取当前页面
		// var page = this.page;
	},
	
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			currentTab: '', // 切换 list 0/1
			menuTabs: [{
				name: '点赞'
			}, {
				name: '评论'
			}],

			// 点赞评论 swiper
			swiperDataList: [
				[], // 点赞 把数据写进里面首次进入页面加载不出，所以写到外面
				[]  // 评论
			],

			screenWidth: 350,
			serverUrl: "",
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			userInfo: '',
			briefDetail: '这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符这里有一百字符一百字符'
		}
	},

	changeIndicatorDots(e) {
		this.indicatorDots = !this.indicatorDots
	},
	changeAutoplay(e) {
		this.autoplay = !this.autoplay
	},
	intervalChange(e) {
		this.interval = e.target.value
	},
	durationChange(e) {
		this.duration = e.target.value
	},


	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
			if (this.currentTab == current) {
				return false;
			} else {
				this.currentTab = current;
				this.setScrollLeft(current);
			}
		},
		swiperChange: async function(e) {
			let index = e.target.current;
			this.setScrollLeft(index);
			this.currentTab = index;
		},
		setScrollLeft: async function(tabIndex) {
			let leftWidthSum = 0;
			for (var i = 0; i <= tabIndex; i++) {
				let nowElement = await this.getWidth('tabNum' + i);
				leftWidthSum = leftWidthSum + nowElement.width;
			}
			let winWidth = uni.getSystemInfoSync().windowWidth;
			this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
		},
		getWidth: function(id) { //得到元素的宽高
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			})
		},
		loadMore: function(tabIndex) {
			console.log('正在加载更多数据。。。')
			this.getDateList(tabIndex);
		},

		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
			// uni.showToast({
			// 	icon: "none",
			// 	title: "回到顶部喽~"
			// })
		}
	}
}
